import { Component } from 'react';
import { appReq, googleMapInit } from '../helpers';
import cities from '../static/json/cities.json';
import mapStyle from '../static/json/map-style.json';

class App extends Component {
  state = {
    map: null,
    mapCenter: {
      lat: 51.026,
      lng: 11.5551,
    },
    cities: [],
  };
  componentDidMount() {
    this.setState({
      cities,
    });

    if (!this.state.map) {
      googleMapInit()
        .then(() => {
          return this.onLoadGoogleMapsScript();
        })
        .then(responses => {
          responses.forEach(response => {
            if (response) {
              const {
                weather: [{ icon }],
                coord: { lat, lon },
              } = response.data;
              this.createMarkers(lat, lon, icon);
            }
          });
        });
    }
  }

  createMarkers(lat, lng, icon) {
    const iconBase = 'http://openweathermap.org/img/w';
    const iconSize = {
      width: 50,
      height: 50,
    };

    const iconObj = {
      url: `${iconBase}/${icon}.png`,
      scaledSize: new google.maps.Size(iconSize.width, iconSize.height),
      anchor: new google.maps.Point(iconSize.width / 2, iconSize.width + 6),
    };

    // Create markers.
    const marker = new google.maps.Marker({
      position: new google.maps.LatLng(lat, lng),
      icon: iconObj,
      map: this.state.map,
    });
  }

  onLoadGoogleMapsScript() {
    const { mapCenter, cities } = this.state;
    const { lat, lng } = mapCenter;

    const nowHour = new Date().getHours();

    this.setState({
      map: new google.maps.Map(document.getElementById('map'), {
        center: { lat, lng },
        zoom: 4,
        minZoom: 5,
        maxZoom: 8,
        styles: nowHour >= 18 && nowHour < 6 ? mapStyle.night : mapStyle.day,
      }),
    });

    const promiseCities = cities.map(city => {
      return appReq.get(`/get-weather?latlng=${city.lat},${city.lng}`);
    });

    return Promise.all(promiseCities);
  }

  render() {
    return (
      <div id="map-wrapper">
        <div id="map" />
        <style global jsx>
          {`
            html,
            body,
            #__next {
              width: 100%;
              height: 100%;
              margin: 0;
              padding: 0;
            }
            #map-wrapper,
            #map {
              display: flex;
              width: 100%;
              height: 100%;
            }
          `}
        </style>
      </div>
    );
  }
}

export default App;
